import React, {
    useState,
    useEffect,
    createContext
}  from 'react'
import { ConfigConsumer } from './Config'
import { getCookie } from '../common'

const DiscoverContext = createContext({})
const { Provider, Consumer } = DiscoverContext

const fetchDiscoverProducts = (updatePdtEvent, Config) => {
    const DiscoverData = Config.discover || {}
    const endPoint = DiscoverData.endPoint ? DiscoverData.endPoint : Config.url
    const discoverPageUrl = DiscoverData.path ? DiscoverData.path : ''
    let API_KEY = ''
    if(DiscoverData.API_KEY) {
        API_KEY = Config.API_KEY
    }
    else {
        API_KEY = Config.API_KEY
    }
    const UUID = getCookie('mad_UUID')
    let userId = localStorage.getItem('userId')
    if(!userId) {
        userId = Config.user_id
    }
    const postData = `api_key=${API_KEY}&user_id=${userId}&details=true&num_results=50&uuid=${UUID}&is_from_demo_site=true&fields=["id","product_id","category","ontology","link","image_link","small_image_link","internal_image_url"]`
    const constructedURL = endPoint + discoverPageUrl
    if(constructedURL && API_KEY && UUID) {
        fetch(
            constructedURL,
            {
                headers: {
                    'Content-type': 'application/x-www-form-urlencoded',
                },
                method: 'POST',
                body: postData
            }
        )
        .then(data => data.json())
        .then(resp => {
            if(resp && resp.data && resp.data.length) {
                updatePdtEvent(resp)
            }
        })
        .catch(err => console.log(err))
    }
}

const DiscoverContainer = props => {
    const {
        children
    } = props
    const [ discoverProds, updateDiscoverProds ] = useState({})
    const [ config, updateConfig ] = useState({})
    
    useEffect(() => fetchDiscoverProducts(updateDiscoverProds, config), [config])
    return <ConfigConsumer>
        {context => {
            updateConfig(context)
            return <Provider value={discoverProds}>
                {children}
            </Provider>
        }}
    </ConfigConsumer>
}

export {
    DiscoverContainer,
    Consumer as DiscoverConsumer
}