import React, {
    createContext,
    useState,
    useMemo,
    useEffect
} from 'react'

const CategoryPdtsContext = createContext({})
const { Consumer, Provider } = CategoryPdtsContext

const fetchCategoryProducts = (updateCategoryPdts, APIData) => {
    if(APIData) {
        const {
            postBody,
            url
        } = APIData
        if(url && postBody) {
            fetch(
                url,
                {
                    method: 'POST',
                    headers: {
                        'Content-type': 'application/json',
                    },
                    body: JSON.stringify(postBody)
                }
            )
            .then(data => data.json())
            .then(resp => {
                if(resp && resp.status && resp.status.toLowerCase() === 'success') {
                    updateCategoryPdts({ ...resp })
                }
                else {
                    updateCategoryPdts({status: 'failure'})
                }
            })
            .catch(err => console.log(err))
        }    
    }
    else {
        updateCategoryPdts({})
    }
}

const CategoryPdtsContainer = props => {
    const {
        children,
        APIData,
        currPage
    } = props

    const [ ProductRecommendation, updateProductRecommendation ] = useState({})

    useMemo(() => fetchCategoryProducts(updateProductRecommendation, APIData), [APIData])
    useEffect(() => updateProductRecommendation({}), [currPage])

    return <Provider value={ProductRecommendation}>
        {children}
    </Provider>
}

export {
    CategoryPdtsContainer,
    CategoryPdtsContainer as default,
    Consumer as CategoryPdtsConsumer
}