import React from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { CardBlock } from "../../";
import { PdtCarouselWrapper, PlaceholderCont } from "./styled";

const RenderPlaceholderData = (props) => {
  const { sliderPerGroup } = props;
  let PlaceholderArr = [];
  for (let i = 0; i < sliderPerGroup; i++) {
    PlaceholderArr.push(<CardBlock key={`placeholder ${i}`} />);
  }
  return PlaceholderArr;
};

const ProductCarousel = (props) => {
  const {
    list,
    sliderPerGroup,
    ItemCont,
    loading,
    showBuyButton,
    showWishlistButton,
    ...remainingProps
  } = props;
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: sliderPerGroup,
    slidesToScroll: sliderPerGroup,
  };

  return (
    <PdtCarouselWrapper>
      {loading ? (
        <PlaceholderCont>
          <RenderPlaceholderData sliderPerGroup={sliderPerGroup} />
        </PlaceholderCont>
      ) : (
        <Slider {...settings}>
          {list &&
            list.map((Item, key) =>
              ItemCont ? (
                <ItemCont
                  {...remainingProps}
                  {...Item}
                  key={`recommendedPdts_${key}`}
                />
              ) : (
                <CardBlock
                  showBuyButton={showBuyButton}
                  showWishlistButton={showWishlistButton}
                  {...remainingProps}
                  {...Item}
                  key={`recommendedPdts_${key}`}
                />
              )
            )}
        </Slider>
      )}
    </PdtCarouselWrapper>
  );
};

export { ProductCarousel };
