import React, {
    useState,
    useEffect,
    createContext
}  from 'react'
import { ConfigConsumer } from './Config'

const PdtDetailsContext = createContext([])
const { Provider, Consumer } = PdtDetailsContext

const fetchProductInfo = (updatePdtEvent, pdtId, force_debug, Config) => {
    const ProductDetailsData = Config.productDetails || {}
    const endPoint = ProductDetailsData.endPoint || Config.url
    const discoverPageUrl = ProductDetailsData.productInfo || Config.url
    let API_KEY = ''
    if(ProductDetailsData.API_KEY) {
        API_KEY = ProductDetailsData.API_KEY
    }
    else {
        API_KEY = Config.API_KEY
    }
    const constructedURL = endPoint + discoverPageUrl
    const postData = `api_key=${API_KEY}&product_id=${pdtId}&get_tags=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link"]&details=true&is_from_demo_site=true&force_debug=true`
    
    if(constructedURL && API_KEY && pdtId) {
        fetch(
            constructedURL,
            {
                headers: {
                    'Content-type': 'application/x-www-form-urlencoded',
                },
                method: 'POST',
                body: postData
            }
        )
        .then(data => data.json())
        .then(resp => {
            if(resp && resp.status && resp.status.toLowerCase() === 'success' && resp.data && resp.data.length) {
                updatePdtEvent(resp.data)
            }
        })
        .catch(err => console.log(err))
    }
}

const ProductDetailsContainer = props => {
    const {
        children,
        productId,
        force_debug
    } = props
    const [ pdtDetailsData, updatepdtDetailsData ] = useState([])
    const [ config, updateConfig ] = useState([])
    
    useEffect(() => fetchProductInfo(updatepdtDetailsData, productId, force_debug, config), [productId, force_debug, config])

    return <ConfigConsumer>
        {context => {
            updateConfig(context)
            return <Provider value={pdtDetailsData}>
                {children}
            </Provider>
        }}
    </ConfigConsumer>
}

export {
    ProductDetailsContainer,
    Consumer as ProductDetailsConsumer
}