const DataInfo = (props) => {
  const {
    product_id,
    title,
    ontology,
    price,
    msd_ontology,
    hideProductId,
    hideTitle,
    hideOntology,
    hidePrice,
    hideMSDOntology,
  } = props;
  return (
    <DataInfoBlock>
      {hideProductId ? null : <span>Product ID : {product_id}</span>}
      {hideTitle ? null : <span>Title : {title}</span>}
      {hideOntology ? null : <span>Ontology : {ontology}</span>}
      {hidePrice ? null : <span>Price : {price}</span>}
      {hideMSDOntology ? null : <span>MSD Ontology : {msd_ontology}</span>}
    </DataInfoBlock>
  );
};

const addToCartHandler = (
  image_link,
  title,
  product_id,
  price,
  updateAddToCartText,
  updateCartPdtCount
) => {
  let all_items_in_cart = localStorage.getItem("cart_items");
  if (
    all_items_in_cart != undefined &&
    all_items_in_cart != "" &&
    all_items_in_cart != "{}"
  ) {
    all_items_in_cart = JSON.parse(all_items_in_cart);
    if (!(product_id in all_items_in_cart)) {
      all_items_in_cart[product_id] = {
        image_link: image_link,
        title: title,
        product_id: product_id,
        price: price,
      };
      localStorage.setItem("cart_items", JSON.stringify(all_items_in_cart));
      updateAddToCartText("Added");
      updateCartPdtCount({ count: Object.keys(all_items_in_cart).length });
    } else {
      for (let key in all_items_in_cart) {
        if (key == product_id) {
          delete all_items_in_cart[product_id];
          localStorage.setItem("cart_items", JSON.stringify(all_items_in_cart));
          updateCartPdtCount({ count: Object.keys(all_items_in_cart).length });
        }
      }
      updateAddToCartText("Add to cart");
    }
  } else {
    all_items_in_cart = {};
    all_items_in_cart[product_id] = {
      image_link: image_link,
      title: title,
      product_id: product_id,
      price: price,
    };
    localStorage.setItem("cart_items", JSON.stringify(all_items_in_cart));
    updateCartPdtCount({ count: Object.keys(all_items_in_cart).length });
  }
};

const addToWishlistHandler = (
  image_link,
  title,
  product_id,
  price,
  updateWishlistIcon,
  updateWishlistPdtCount
) => {
  let all_items_in_wishlist = localStorage.getItem("wishlist_items");
  if (
    all_items_in_wishlist != undefined &&
    all_items_in_wishlist != "" &&
    all_items_in_wishlist != "{}"
  ) {
    all_items_in_wishlist = JSON.parse(all_items_in_wishlist);
    if (!(product_id in all_items_in_wishlist)) {
      all_items_in_wishlist[product_id] = {
        image_link: image_link,
        title: title,
        product_id: product_id,
        price: price,
      };
      localStorage.setItem(
        "wishlist_items",
        JSON.stringify(all_items_in_wishlist)
      );
      updateWishlistIcon("heart");
      updateWishlistPdtCount({
        count: Object.keys(all_items_in_wishlist).length,
      });
    } else {
      for (let key in all_items_in_wishlist) {
        if (key == product_id) {
          delete all_items_in_wishlist[product_id];
          localStorage.setItem(
            "wishlist_items",
            JSON.stringify(all_items_in_wishlist)
          );
          updateWishlistPdtCount({
            count: Object.keys(all_items_in_wishlist).length,
          });
        }
      }
      updateWishlistIcon("heart outline");
    }
  } else {
    all_items_in_wishlist = {};
    all_items_in_wishlist[product_id] = {
      image_link: image_link,
      title: title,
      product_id: product_id,
      price: price,
    };
    localStorage.setItem(
      "wishlist_items",
      JSON.stringify(all_items_in_wishlist)
    );
    updateWishlistIcon("heart");
    updateWishlistPdtCount({
      count: Object.keys(all_items_in_wishlist).length,
    });
  }
};

const CardBlock = (props) => {
  const {
    image_link,
    internal_image_url,
    title,
    product_id,
    debug_info,
    force_debug,
    showBuyButton,
    showWishlistButton,
    price,
    ...remainingProps
  } = props;
  const redirectLink = `/product-display/?id=${product_id}`;
  const redirectToPdp = (product_id) => {
    window.location.href = `/product-display/?id=${product_id}`;
  };

  const renderCartText = (product_id) => {
    let all_items_in_cart = localStorage.getItem("cart_items");
    all_items_in_cart = JSON.parse(all_items_in_cart);
    if (product_id != undefined && all_items_in_cart != null) {
      if (product_id in all_items_in_cart) {
        return "Added";
      } else {
        return "Add to cart";
      }
    } else {
      return "Add to cart";
    }
  };

  const renderWishListIcon = (product_id) => {
    let all_items_in_wishlist = localStorage.getItem("wishlist_items");
    all_items_in_wishlist = JSON.parse(all_items_in_wishlist);
    if (product_id != undefined && all_items_in_wishlist != null) {
      if (product_id in all_items_in_wishlist) {
        return "heart";
      } else {
        return "heart outline";
      }
    } else {
      return "heart outline";
    }
  };

  const renderWishListIconColor = (product_id) => {
    let all_items_in_wishlist = localStorage.getItem("wishlist_items");
    all_items_in_wishlist = JSON.parse(all_items_in_wishlist);
    if (product_id != undefined && all_items_in_wishlist != null) {
      if (product_id in all_items_in_wishlist) {
        return "red";
      } else {
        return "grey";
      }
    } else {
      return "grey";
    }
  };

  const [imgLoadStatus, updateImgLoadStatus] = useState(false);
  const [addToCartText, updateAddToCartText] = useState(
    renderCartText(product_id)
  );
  const [wishListIcon, updateWishlistIcon] = useState(
    renderWishListIcon(product_id)
  );

  return (
    <DataItemBlockEl debugMode={debug_info ? true : false}>
      <CarouselItemWrapper>
        {/* <DataImageBlock to={redirectLink}> */}
        <DataImageBlock>
          <div
            style={{
              width: "100%",
              height: "90%",
              borderWidth: "1px",
              borderStyle: "solid",
              borderColor: "lightgray",
              background: "#fff",
              position: "relative",
            }}
          >
            <ImageContainer>
              <DataItemImg
                src={
                  imgLoadStatus
                    ? internal_image_url || image_link || LoaderImg
                    : LoaderImg
                }
                alt={title || "Data Image"}
                onLoad={(e) => updateImgLoadStatus(true)}
                onError={(e) => updateImgLoadStatus(true)}
              />
            </ImageContainer>
            {showWishlistButton ? (
              <HeaderConfigConsumer>
                {(context) => {
                  const { updateWishlistPdtCount } = context[1];
                  return (
                    <WishlistIconContainer>
                      <IconWapper
                        onClick={() => redirectToPdp(product_id)}
                      ></IconWapper>
                      <div
                        style={{
                          height: "35px",
                          width: "30px",
                          position: "absolute",
                          display: "flex",
                          justifyContent: "center",
                          alignItems: "center",
                          top: "0px",
                          right: "0px",
                        }}
                        onClick={() =>
                          addToWishlistHandler(
                            image_link,
                            title,
                            product_id,
                            price,
                            updateWishlistIcon,
                            updateWishlistPdtCount
                          )
                        }
                      >
                        <Icon
                          name={wishListIcon}
                          // name={renderWishListIcon(product_id)}
                          // onClick={() =>
                          //   addToWishlistHandler(
                          //     image_link,
                          //     title,
                          //     product_id,
                          //     price,
                          //     updateWishlistIcon,
                          //     updateWishlistPdtCount
                          //   )
                          // }
                          size="large"
                          color={renderWishListIconColor(product_id)}
                          style={{}}
                        />
                      </div>
                    </WishlistIconContainer>
                  );
                }}
              </HeaderConfigConsumer>
            ) : null}
          </div>
        </DataImageBlock>
      </CarouselItemWrapper>
      {showBuyButton ? (
        <HeaderConfigConsumer>
          {(context) => {
            const { updateCartPdtCount } = context[0];
            return (
              <RecommendationBtn
                onClick={() =>
                  addToCartHandler(
                    image_link,
                    title,
                    product_id,
                    price,
                    updateAddToCartText,
                    updateCartPdtCount
                  )
                }
              >
                {addToCartText}
              </RecommendationBtn>
            );
          }}
        </HeaderConfigConsumer>
      ) : null}
      <TitleBlock to={redirectLink}>{title}</TitleBlock>
      {debug_info && force_debug ? <DebugBlock {...debug_info} /> : null}
    </DataItemBlockEl>
  );
};

export { CardBlock };
