import React, { createContext, useState }  from 'react'

const ConfigContext = createContext({})
const { Provider, Consumer } = ConfigContext

const ConfigContainer = props => {
    const {
        children
    } = props
    const AuthConfig = localStorage.getItem('userConfig')
    const JSONAuthConfig = AuthConfig && AuthConfig.trim().length ? JSON.parse(AuthConfig) : {}
    const [ Config, updateConfig ] = useState(Object.keys(JSONAuthConfig).length ? JSONAuthConfig : {})
    Config.updateConfig = updateConfig

    return <Provider value={Config}>
        {children}
    </Provider>
}

export {
    ConfigContext,
    ConfigContainer,
    Consumer as ConfigConsumer
}