import React from "react";
import { ListWrapper } from "./styled.jsx";

const ListElement = (props) => {
  const {
    list,
    ItemEl,
    showBuyButton,
    showWishlistButton,
    ...remainingProps
  } = props;

  return (
    <ListWrapper>
      {list.map((item, index) => {
        return (
          <ItemEl
            showBuyButton={showBuyButton}
            showWishlistButton={showWishlistButton}
            {...item}
            {...remainingProps}
            key={`list_${index}`}
          />
        );
      })}
    </ListWrapper>
  );
};

export { ListElement };
