import React, {
    useState
} from 'react'
import { RenderSpiderWrapper } from './renderProps'
import { RecommendationSpiderWrapper } from './styled'

const SpiderBlock = props => {
    const {
        recommendedData
    } = props
    let spiderData = ''
    let spiderCaption = ''
    let bandit_data = props['recommendedData']['0_enableBundit']
    if(bandit_data){
        if(bandit_data[0]['debug_info'] &&  bandit_data[0]['debug_info']['probability']){
            spiderData = bandit_data[0]['debug_info']['probability']
            spiderCaption = {
                // columns
                0: 'colour & pattern',
                1: 'colour & shape',
                2: 'pattern',
                3: 'colour',
                4: 'pattern & shape',
                5: 'shape'
            };
        }
    }
    
    
    
    return (bandit_data && spiderData && Object.keys(spiderData).length > 0 && Object.keys(spiderData).length == 6) ? <RecommendationSpiderWrapper>
        <RenderSpiderWrapper spiderCaption={spiderCaption} spiderData={spiderData} />
    </RecommendationSpiderWrapper> : null
}

export { SpiderBlock }

