import React from 'react'
import {SpiderWrapper} from  './styled'
import RadarChart from 'react-svg-radar-chart';
import 'react-svg-radar-chart/build/css/index.css'

const RenderSpiderWrapper = props => {
    const {
        spiderData,
        spiderCaption
    } = props
    let spiderCustomData = [{'data': spiderData, 'meta': { color: 'red' }}]
    return <SpiderWrapper>
        <RadarChart 
        captions={spiderCaption}
        data={spiderCustomData}
        size={300} 
        />     
    </SpiderWrapper> 

}

export {
    RenderSpiderWrapper
}