import React, {
    createContext,
    useState,
    useMemo
} from 'react'

const RecommendedPdtsContext = createContext({})
const { Consumer, Provider } = RecommendedPdtsContext

const fetchRecommendedProducts = (updateRecommendedPdts, recommendedWidgetList) => {
    const {
        postBody,
        widgetList,
        recommendationUrl
    } = recommendedWidgetList
    
    if(recommendationUrl && widgetList && postBody) {
        fetch(
            recommendationUrl,
            {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
                },
                method: 'POST',
                body: recommendedWidgetList.postBody
            }
        )
        .then(data => data.json())
        .then(resp => {
            let updatedRecommendedObj = {
                loading: false
            }
            if(resp && resp.status && resp.status.toLowerCase() === 'success' && resp.data) {
                if(resp.data.length && Array.isArray(resp.data)){
                    resp.data.map((item, key) => {
                        if(item && item.length && recommendedWidgetList.widgetList[key]) {
                            updatedRecommendedObj[recommendedWidgetList.widgetList[key]] = [...item]
                        }
                        return item
                    })
                } else {
                    updatedRecommendedObj[recommendedWidgetList.widgetList] = resp.data
                }
                
            }
            updateRecommendedPdts(prevRecommendedObj => ({...prevRecommendedObj, ...updatedRecommendedObj}))
        })
        .catch(err => 
            console.log(err)
        )
    }
    updateRecommendedPdts({
        loading: true
    })
}

const RecommendedPdtsContainer = props => {
    const {
        children,
        recommendedWidgetList
    } = props
    const [ ProductRecommendation, updateProductRecommendation ] = useState({
        loading: true
    })

    useMemo(() => fetchRecommendedProducts(updateProductRecommendation, recommendedWidgetList), [recommendedWidgetList])

    return <Provider value={ProductRecommendation}>
        {children}
    </Provider>
}

export {
    RecommendedPdtsContainer,
    RecommendedPdtsContainer as default,
    Consumer as RecommendedPdtsConsumer
}